import { usePostIds, usePosts } from "../hooks/posts";
import { Post } from "./Post";

import style from "../style/post.module.sass";
import { useState, useEffect } from "react";

export function PostList(props) {
    var postIds = usePostIds(props.rerender);
    var posts = usePosts(postIds);
    var refs = [];

    var postObjects = posts.map((post) => {
        return (<Post key={post.entity.id} post={post} ref={(ref) => refs.push(ref)}></Post>)
    });

    useEffect(() => {

    }, [postIds]);

    // <button onClick={() => {window.supersecrettrashusers()}}>Accs</button>
    return (
        <div className={style.postList}>
            
            {postObjects}
        </div>
    );
}